import { Component, OnInit } from '@angular/core';
import { TripsService } from '../../../core/services/trips.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Subscription } from 'rxjs';
import { Trip } from '../../../core/models/trip.model';
import { TripEditComponent } from '../trip-edit/trip-edit.component';

@Component({
  selector: 'app-trips',
  templateUrl: './trips-list.component.html'
})
export class TripsListComponent implements OnInit {
  trips$: Trip[];

  subscriptions: Subscription = new Subscription();

  constructor(
    private tripsService: TripsService,
    private modalService: NgbModal
  ) { }

  ngOnInit() {
    this.subscriptions.add(this.tripsService.getTripsList().subscribe(
      res => {
        this.trips$ = res;
      }
    ));
  }

  addNewTrip() {
    const modalRef = this.modalService.open(TripEditComponent, {size: 'lg'});
    modalRef.componentInstance.updateList.subscribe((receivedEntry) => {
      this.trips$ = receivedEntry;
    });
  }

  editTrip(id: string) {

  }

  deleteTrip(id: string) {

  }

}
