import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';
import { Trip } from '../models/trip.model';

const SERVER_URL = 'http://localhost:20855/api';

@Injectable({
  providedIn: 'root'
})
export class TripsService {

  constructor(private http: HttpClient) { }

  getTripsList(): Observable<Trip[]> {
    return this.http.get<Trip[]>(SERVER_URL + '/Trips/getTripsList');
  }

  getTripById(tripId: string): Observable<Trip> {
    return this.http.get<Trip>(SERVER_URL + '/Trips/tripId/' + tripId);
  }

  saveTrip(trip: Trip): Observable<Trip[]> {
    return this.http.post<Trip[]>(SERVER_URL + '/Trips/createTrip', trip);
  }

  updateTrip(trip: Trip): Observable<Trip[]> {
    return this.http.post<Trip[]>(SERVER_URL + '/Trips/updateTrip', trip);
  }

  deleteTrip(newsId: string): Observable<Trip[]> {
    return this.http.get<Trip[]>(SERVER_URL + '/Trips/deleteTrip/' + newsId);
  }
}
